body,
html {
  width: 100%;
  height: 100%; }

ul {
  list-style: none;
  padding: 0;
  margin: 0; }

li {
  padding: 0;
  margin: 0; }

a {
  color: white;
  text-decoration: none; }

.wrapper {
  display: flex;
  width: 100%;
  height: 100%;
  min-width: 1000px;
  overflow: hidden; }

.sidebar {
  max-width: 400px;
  min-width: 280px;
  background-color: #ffffff;
  display: flex;
  flex-direction: column;
  align-items: center; }

.logo {
  font-size: 20px;
  font-weight: bold;
  color: #84a5ff;
  margin: 40px auto 80px; }

.nav-item a {
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 123px;
  padding: 12px 54px;
  margin-bottom: 16px;
  color: #333333;
  font-size: 16px; }

.nav-item:hover a {
  color: #ffffff;
  background-color: rgba(132, 165, 255, 0.5); }

.nav-item.active a {
  color: #ffffff;
  background-color: #84a5ff; }

.view-mode-list {
  display: flex; }

.theme-view-mode-list {
  margin: auto auto 120px; }

.view-mode {
  width: 32px;
  height: 32px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  border: none;
  outline: none;
  background-color: rgba(51, 51, 51, 0.1);
  color: #ffffff; }
  .view-mode:hover {
    background-color: rgba(132, 165, 255, 0.5); }
  .view-mode.active {
    background-color: #84a5ff; }
  .view-mode:not(:last-child) {
    margin-right: 8px; }
  .view-mode img {
    pointer-events: none; }

.main {
  flex: 1;
  background-color: #f2f4f6;
  padding: 40px 80px;
  overflow: auto; }

.main-header {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px; }

.advanced-search {
  display: flex;
  flex-wrap: wrap; }
  .advanced-search .select {
    width: 300px;
    margin-bottom: 16px;
    margin-right: 20px; }

.select {
  outline: none;
  border: none;
  border-radius: 7px;
  background-color: #ffffff;
  color: #333333;
  display: block;
  padding: 14px 24px;
  width: 100%;
  height: 48px;
  line-height: 1.5; }

.item-view-mode-list .view-mode {
  border-radius: 4px;
  background-color: #ffffff;
  color: rgba(51, 51, 51, 0.1);
  font-size: 18px; }
  .item-view-mode-list .view-mode:hover {
    background-color: rgba(132, 165, 255, 0.5);
    color: #ffffff; }
  .item-view-mode-list .view-mode.active {
    background-color: #84a5ff;
    color: #ffffff; }

.note-list {
  display: flex;
  flex-wrap: wrap;
  margin-left: -10px;
  margin-right: -10px; }

.note {
  width: 300px;
  border-radius: 7px;
  padding: 16px 16px 24px;
  background-color: #ffffff;
  box-shadow: 0px 0px 4px #0000000d;
  margin: 10px; }
  .note:hover {
    cursor: pointer;
    box-shadow: 0 8px 8px 0 rgba(0, 0, 0, 0.2); }

.note-header {
  height: 120px;
  overflow: hidden;
  border-radius: 5px;
  margin-bottom: 16px; }

.note-preview-img {
  width: 100%;
  height: 100%;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat; }

.note-body {
  display: flex; }

.like-note-button {
  background-color: transparent;
  color: #333333;
  opacity: 0.1;
  border: none;
  outline: none;
  width: 24px;
  height: 24px;
  margin-top: 4px;
  margin-right: 8px;
  display: flex;
  justify-content: center;
  align-items: center; }
  .like-note-button:hover, .like-note-button.active {
    color: #84a5ff;
    opacity: 1; }

.note-content {
  flex: 1; }

.note-title {
  color: #333333;
  font-size: 20px;
  font-weight: 500;
  text-align: center;
  margin-bottom: 16px; }

.note-description {
  display: flex;
  justify-content: center;
  align-items: center; }

.note-date {
  color: rgba(51, 51, 51, 0.5);
  font-size: 14px; }

.note-tag {
  background-color: #e6edff;
  color: #84a5ff;
  font-size: 12px;
  border-radius: 4px;
  padding: 2.5px 12px;
  margin-left: 16px; }

.dark .sidebar {
  background-color: #2f3136; }

.dark .nav-item a {
  color: #ffffff; }

.dark .view-mode {
  background-color: rgba(255, 255, 255, 0.2); }
  .dark .view-mode:hover {
    background-color: rgba(132, 165, 255, 0.5); }
  .dark .view-mode.active {
    background-color: #84a5ff; }

.dark .main {
  background-color: #25272c; }

.dark .select {
  background-color: #2f3136;
  color: #ffffff; }

.dark .item-view-mode-list .view-mode {
  background-color: #393b40;
  color: rgba(255, 255, 255, 0.1); }
  .dark .item-view-mode-list .view-mode:hover {
    background-color: rgba(132, 165, 255, 0.5);
    color: #ffffff; }
  .dark .item-view-mode-list .view-mode.active {
    background-color: #84a5ff;
    color: #ffffff; }

.dark .note {
  background-color: #393b40; }

.dark .like-note-button {
  color: #ffffff; }
  .dark .like-note-button:hover, .dark .like-note-button.active {
    color: #84a5ff; }

.dark .note-title {
  color: #ffffff; }

.dark .note-date {
  color: rgba(255, 255, 255, 0.5); }
